import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppComponent } from './app.component';
import { GclHeaderComponent } from './gcl-header/gcl-header.component';
import { GclMenuComponent } from './gcl-menu/gcl-menu.component';
import { GclSplashComponent } from './gcl-splash/gcl-splash.component';
import { GclFooterComponent } from './gcl-footer/gcl-footer.component';
import { RouterModule } from '@angular/router';
import { GclHtmlPageComponent } from './gcl-html-page/gcl-html-page.component';
import { GclJavaPageComponent } from './gcl-java-page/gcl-java-page.component';
import { GclAndroidPageComponent } from './gcl-android-page/gcl-android-page.component';
import { GclCommonFooterComponent } from './gcl-common-footer/gcl-common-footer.component';

@NgModule({
  declarations: [
    AppComponent,
    GclHeaderComponent,
    GclMenuComponent,
    GclSplashComponent,
    GclFooterComponent,
    GclHtmlPageComponent,
    GclJavaPageComponent,
    GclAndroidPageComponent,
    GclCommonFooterComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot([
      {
        path: '',
        redirectTo: 'home',
        pathMatch: 'full'
      },
      {
        path: 'home',
        component: GclFooterComponent
      },
      {
        path: 'html',
        component: GclHtmlPageComponent
      },
      {
        path: 'java',
        component: GclJavaPageComponent
      },
      {
        path: 'android',
        component: GclAndroidPageComponent
      }
    ])
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



