import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';

@Component({
  selector: 'app-gcl-menu',
  templateUrl: './gcl-menu.component.html',
  styleUrls: ['./gcl-menu.component.css']
})
export class GclMenuComponent implements OnInit {

  constructor(private route: Router) { }

  ngOnInit() {
  }

  toHtml() {
    this.route.navigate(['/html']);
  }

  toJava() {
    this.route.navigate(['/java']);
  }

  toAndroid() {
    this.route.navigate(['/android']);
  }
}



